












import Vue from 'vue';
import Table from './models/table';
import Player from './models/player';
export default Vue.extend({
    name: 'App',
    data() {
        return {
            table: {} as Table,
        };
    },
    methods: {
        setPlayer(players: Player[]) {
            this.table.players = players;
        },
        setTable(table: Table) {
            this.table = table;
        },
    },
    created: function () {
        this.table = new Table();
    },
});
